/**
 * border radius
 */

.btn,
.jumbotron,
.nav :hover {
  border-radius: 0px;
}

* {
  transition: all .3s;
}

/* cart badge */

.cart-badge,
.cart-badge:hover {
  border-radius: 50%;
  padding: 2px;
  font-size: 12px;
  font-style: italic;
  background: #000;
}
/**
  * single product page - product name
  */

.name {
  background: indigo;
  color: #fff;
  font-weight: bold;
}

/* black shade form 10-1 */
.black-10 {
  background: #f2f2f2;
}
.black-9 {
  background: #e6e6e6;
}
.black-8 {
  background: #d9d9d9;
}
.black-7 {
  background: #cccccc;
}
.black-6 {
  background: #bfbfbf;
}
.black-5 {
  background: #b3b3b3;
}

/**
  * product image on card
  */

#root {
  background-color: black;
}

.menu {
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 20px;
  background-color: black;
}
.menu * {
  transition: all .3s;;
}

.menu svg {
  color: white;
  cursor: pointer;
}

.jumbotron h2 {
  margin-top: -20px;
}

.main-content {
  max-width: 900px;
  margin: auto;
}

.left-menu {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  padding: 5px;
}
.left-menu-content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  border-radius: 11px;
  padding: 1px;
  height: 22px;
}

.left-menu-content>div{
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left-menu-content svg {
  width: 15px;
  height: 15px;
}

.left-menu-content .selected {
  border-radius: 10px;
  background-color: rgb(100, 100, 100);
}

.right-menu {
  display: flex;
  align-items: center;
}

.menu-item {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.menu-item:hover svg {
  color: rgb(150, 150, 150);
}

.menu-item svg {
  width: 50px;
  height: 50px;
  color: white;
}

.shop-item {
  width: 33%;
  padding: 20px;
  cursor: pointer;
}

.product-img {
  min-height: 100px;
  max-height: 250px;
  height: 30vw;
  border: 2px solid rgb(66, 133, 245);
  overflow: hidden;
  border-radius: 10px;
}

.product-img:hover img {
  transform: scale(1.1);
}

.shop-item.selected .product-img {
  border-width: 15px;
  border-radius: 15px;
}

.item-name {
  color: rgb(66, 133, 245);
  font-weight: bold;
  font-size: 15px;
  margin-right: 10px;
}

.insert-link svg {
  color: rgb(150, 150, 150);
  transform: rotate(-45deg);
}

.insert-link:hover svg {
  opacity: 0.7;
}

.item-cart, .item-user {
  background-color: rgb(200, 200, 200);
  border-radius: 5px;
  padding: 5px;
  margin-left: 5px;
  cursor: pointer;
}

.more-btn {
  background-color: black !important;
  color: rgb(66, 133, 245) !important;
  font-size: 30px !important;
  font-weight: bold !;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  outline: none !important;
}
.more-btn svg {
  width: 40px;
  height: 40px;
}

.sub-menu {
  padding-right: 10px;
}

.total-price {
  color: #4285f5;
  margin-right: 10px;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.checkout-btn {
  display: flex;
  align-items: center;
}

.checkout-btn svg {
  height: 40px;
  width: 40px;
}

.card-container {
  position: relative;
}

.link {
  position: absolute;
  top: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #4285f570;
  justify-content: center;
  align-items: center;
  display: none;
}

.link.left {
  left: 15px;
}
.link.right {
  right: 15px;
}

.card-container:hover .link, .selected .link.left {
  display: flex;
}

.link:hover {
  background-color: #4285f550;
}
.selected .link {
  background-color: #4285f5A0;  
}

.link svg {
  color: gray;
  width: 40px;
  height: 40px;
}

@media only screen and (max-width: 600px) {
  .jumbotron h2 {
    margin-top: 10px;
  }
}

/**
  * jumbotron animation
  */

.jumbotron {
  height: 180px;
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  -moz-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@-moz-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
